.container {
    margin-left: 15px;
    padding-left: 15px;
    max-width: 300px;
    display: flex;
    background-color: #5dda5d;
    border-radius: 15px;
    align-items: flex-end;
}

.avatar {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    margin-bottom: 20px;
}
.massegeComtainer {
    flex: 1 1 auto;
}
.massege {
    display: flex;
    flex: 1 1 auto;
    background-color: #103b70;
    color: aliceblue;
    padding: 10px;
    margin: 15px;
    border-radius: 15px;
}

.name {
    color: #31213e;
    color: aliceblue;
}
.time {
    align-self: flex-end;
}